
.tn-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0;
    background: rgb(255, 255, 255);
    font-family: 'Times New Roman', Times, serif;
    box-shadow: 0 1px 20px 0 rgb(27, 10, 0);
}

.logo{
    font-size: 1.5rem ;
    font-weight: 600;
    padding: 0 0.4rem;
    letter-spacing: 0.1rem;
    text-decoration: none;
    color: rgb(27, 10, 0);
}

.search-box:focus{
    border: 1px solid rgb(0, 0, 0);
}

.search-box{
    width: 555px;
    border: 1px solid rgb(163, 160, 160);
    border-radius: 25px;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s ease;
}



.search-box input{
    width: 100%;
    display: block;
    border:none;
    outline: none;
    
}


.search-icon{
    color:rgb(27, 10, 0);
    font-size: 1rem;
    cursor: pointer;
}

.tn-icons{
    display: flex;
    padding: 0 20px;
    align-items: center;
    list-style: none;
    gap: 20px;
    
    font-size: 1rem;
}

.tn-icons a{
    font-size: 1rem;
    color: rgb(27, 10, 0);
}

.fav-icon, .cart-icon{
    position: relative;
}

.cart-icon .count{
    position: absolute;
    font-size: 0.5rem;
    top: -0.34rem;
    right: -0.5rem;
    color:rgb(255, 255, 255);
    background-color:rgb(27, 10, 0);
    border-radius: 25px;
    padding: 0 5px;
}

.bn-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(27, 10, 0);
    height: 3rem;
}

.nav-lists{
    display: flex;
    gap: 1.3rem;
    text-transform: uppercase;
    z-index: 111;
    padding: 0.5rem;
    list-style: none;
}

.nav-lists li a{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 200;
    font-size: 1rem;
    position: relative;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 5px;
    transition: 0.6s ease;
    color: rgb(255, 255, 255);
}

.nav-lists li a:hover{
    color:rgb(27, 10, 0);
    background-color: rgb(255, 255, 255);
}